@charset "UTF-8";


/* 전문가 지식나눔 설문조사 완료 QustnrRegist */
.QustnrRegist{
    .sub_contents{
        width:1280px; margin:40px auto 150px; padding:0 20px;
        @include boxSizing;
        .regist_wrap{
            width:100%; border-top:1px solid $gray4; border-bottom:1px solid $gray6; padding:120px 0;
            .txt_wrap{width:400px; margin:0 auto; padding:110px 0 0; text-align:center; background:url("/img/cs/square/icon_regist.jpg") 50% 0 no-repeat;}
            strong{
                color:$blue1; font-weight:$bold;
                @include fontRul(36px);
            }
            p{
                color:$gray4; font-weight:$bold; margin:30px 0 70px;
                @include fontRul(20px);
            }
            a{
                height:50px; padding:0 60px; font-size:16px; font-weight:$bold; line-height:50px; color:$gray2; border:1px solid $gray5; display:inline-block;
                @include borderRadius(50px);
                @include transition(all);
            }
            a:hover{background-color:$primary; color:$white;}
        }
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:em(40) auto em(150); padding:0 em(30);
            @include boxSizing;
            .regist_wrap{
                width:100%; padding:em(120) 0;
                .txt_wrap{width:100%; padding:110px 0 0; text-align:center; background:url("/img/cs/square/icon_regist.jpg") 50% 0 no-repeat;}
                strong{font-size:em(40); letter-spacing:-.5px; line-height:em(52);}
                p{margin:em(30) 0 em(70); font-size:em(28); line-height:em(40); letter-spacing:0;}
                a{height:em(80); padding:0 em(80); font-size:em(24); line-height:em(80);}
            }
        }
    }
}


/* 시민 참여 광장 시민제보 CtznBoardList */
.CtznBoardList{
    .sub_contents{
        .report_info_wrap{
            width:1280px; margin:0 auto; border-top:1px solid $gray6; border-bottom:1px solid $gray6; padding:50px;
            background:url("/img/cs/square/icon_report_info.jpg") 97% 50% no-repeat;
            @include boxSizing;
            ul{
                margin:0 0 20px;
                li{
                    color:$blue3; font-weight:$bold; padding:0 0 0 13px;
                    @include boxSizing;
                    @include fontRul(16px);
                    &:before{
                        width:4px; height:4px; content:""; display:inline-block; background-color:$blue3; vertical-align:middle; position:relative; left:-10px;
                        @include borderRadius(4px);
                    }
                }
            }
            a{
                width:180px; height:50px; display:inline-block; font-size:16px; letter-spacing:-.5px; line-height:50px; color:$white; font-weight:$bold; text-align:center; margin:0 15px 0 0;
                border:1px solid transparent;
                @include borderRadius(90px);
                @include transition(all);
                @include boxSizing;
                &.complaints_btn{background-color:$gray3;}
                &.complaints_btn:hover{background-color:$white; color:$gray3; border:1px solid $gray3;}
                &.propose_btn{background-color:$blue2;}
                &.propose_btn:hover{background-color:$white; color:$blue2; border:1px solid $blue2;}
            }
        }
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:em(40) em(30) 0;
            .introText p{font-size:em(24); line-height:em(36);}
            .report_info_wrap{
                width:100%; padding:em(30) 0 em(60); background:none; text-align:center;
                ul{
                     text-align:left; border-bottom:1px solid $gray6; padding:0 0 em(40); margin:0 0 em(40);
                    li{font-size:em(24); line-height:em(40); margin:0 0 em(10);}
                }
                a{width:em(280); height:em(86); font-size:em(26); letter-spacing:0; line-height:em(86); margin:0 em(10) 0 0;}
            }
        }
    }
}

/* 설문조사 QustnrResultSearch */
.QustnrResultSearch{
    @include mo(){
        .sub_contents{
            width:100%; margin:em(40) auto em(150); padding:0 em(30);
            .boardListTable > table{width:100%;}
            .boardListTable > table th{width:25%;}
            .boardListTable > table td{width:75%;}
            .boardView .preData {white-space: inherit;}
            .qusSubjects h4{font-size:em(30); letter-spacing:-.5px; line-height:em(52);}
            .qusAnswerLists{padding:em(50); @include boxSizing;}
            .qusAnswerLists.qusAnswerTxt{padding:0;}
            .qusAnswerLists.qusAnswerTxt > textarea{@include boxSizing;}
            .textForm{width:100%; margin:em(20) 0 0;}
        }
    }

}

/* 행사공유 EventList */
.EventList{
    @include mo(){
        .sub_contents{
            width:100%; margin:em(40) auto em(150); padding:0 em(30);
            .eventBtns{display:none;}
            .eventCategoryItems:not(:last-child){margin-bottom:em(20);}
            .boardLists{margin:em(60) 0 0;}
            .lookup_wrapper{margin:0 0 em(200);}
            .monthLists{flex-wrap: wrap;}
            .monthItems:not(:last-child){margin-right:em(20);}
            .monthItems{margin:em(20);}
        }
    }
}

/* 행사공유 EventDetail */
.EventDetail{
    @include mo(){
        .sub_contents{
            width:100%; margin:em(40) auto em(150); padding:0 em(30);
            .boardListTable > table{width:100%; display:block; border-bottom:1px solid $gray6;}
            .boardListTable > table tbody{width:100%; display:block;}
            .boardListTable > table tr{width:100%; background-color:$bg-gray1; border: 1px solid $gray6; border-top:none; border-bottom:none;}
            .boardListTable > table tr:after{display:block; clear:both; content:"";}
            .boardListTable > table th,
            .boardListTable > table td{ display:block; float:left;  border-left:none; border-right:none; border-bottom:none;  @include boxSizing;}
            .boardListTable > table th{width:30%; height:100%;}
            .boardListTable > table td{width:70%; height:auto; background-color:$white; border-left: 1px solid $gray6;}
            .boardView .preData{white-space:inherit; min-height:auto;}
        }
    }
}