@charset "UTF-8";
/* 미세먼지 코디네이터 활동 - 등록 - CoordinatorActRegist */
.CoordinatorActRegist{
    .sub_contents{
        width:1280px; margin:0 auto 150px; padding:20px 20px 0;
        @include boxSizing;
        .codi_banner {height:150px;}
        .resister_wrap {margin:40px 0 0;}
        .upload_info{display: inline; font-size:14px; letter-spacing:-.5px; color:$gray2;}
        .img_disclamer{
            margin:15px 0;
            li{
                color:$gray2; font-weight:$bold;
                @include fontRul(14px);
            }
        }
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:em(20) em(30) 0;
            .basic_table{
                width:100%;
                table{display:block; width:100%;}
                caption{position:absolute; top:0; left:0;}
                colgroup{display:none;}
                tbody{width:100%; display:block;}
                tr{width:100%; border:none; display:block;}
                tr:after{display:block; content:""; clear:both;}
                th{
                    width:25%; display:block; float:left; border-top:1px solid $gray4; font-size:em(24); line-height:em(36); padding:em(40) em(20) 0 ;
                    @include boxSizing;
                }
                td{
                    width:75%; display:block; float:left; border-top:1px solid $gray4; font-size:em(24); line-height:em(36); padding:em(20) 0;
                    @include boxSizing;
                }
                input[type="text"]{
                    width:100% !important; font-size:em(24) !important; line-height:em(36) !important;
                    @include boxSizing;
                }
                .basic_txt_area{
                    width:100% !important; font-size:em(24); line-height:em(36); padding:10px 15px;
                    @include boxSizing;
                }
            }
            .upload_info{width:80%; display:block; font-size:em(22); letter-spacing:0; margin:em(16) 0 0;}
            .img_disclamer{
                margin:em(30) 0;
                li{width:90%; font-size:em(22); letter-spacing:0;}
            }
        }
    }
}
/* 찾아가는 미세먼지 교실 - 등록 - MyEdcSchdulCldrList */
.MyEdcSchdulCldrList{
    .sub_contents{
        width:1280px; margin:0 auto 150px; padding:40px 20px 0;
        @include boxSizing;
        .schdulCldrListLookup_wrap table{
            th,td{border:1px solid $gray6; padding:15px 0 15px 20px;}
            th{background-color:$bg-gray1; font-size:15px; color:$gray2; font-weight:$bold;}
        }
        .schdulCldrList_wrap{margin:70px 0 0;}
        .edc_more{
            font-size:14px; color:$white; background-color:$gray3; padding:10px 16px;
            @include borderRadius(30px);
        }
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:em(20) em(30) 0;
            .schdulCldrListLookup_wrap table{
                width:100%;
                th,td{border:1px solid $gray6; padding:em(20); @include boxSizing;}
                th{background-color:$bg-gray1; font-size:15px; color:$gray2; font-weight:$bold;}
                .radioLabel{margin:em(20);}
                .selectForm{width:100% !important; display:block; margin:0 0 em(20);}
                .textForm{width:100% !important; margin:0 !important;}
            }
            .schdulCldrList_wrap{margin:em(80) 0 0;}
            .edc_more{
                font-size:14px; padding:10px 16px;
                @include borderRadius(30px);
            }
        }
    }
}

/* 찾아가는 미세먼지 교실 - 등록 - UlfptcEdcRegist */
.UlfptcEdcRegist{
    .sub_contents{
        width:1280px; margin:0 auto 80px; padding:20px 20px 0;
        @include boxSizing;
        .codi_banner {height:150px;}
        .resister_wrap {margin:40px 0 0;}
        .upload_info{display: inline; font-size:14px; letter-spacing:-.5px; color:$gray2;}
        .inquiry_btn{
            color:$white; padding:8px 15px; background-color:$gray3; margin:0 0 0 15px;
            @include borderRadius(30px);
        }
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(80); padding:em(20) em(30) 0;
            .basic_table{
                width:100%;
                table{display:block; width:100%;}
                caption{position:absolute; top:0; left:0;}
                colgroup{display:none;}
                tbody{width:100%; display:block;}
                tr{border:none; width:100%; display:block;}
                th{
                    width:30%; display:block; float:left; border-top:1px solid $gray4; font-size:em(24); line-height:em(36); padding:em(40) em(20) 0;
                    @include boxSizing;
                }
                td{
                    width:70%; display:block; float:left; border-top:1px solid $gray4; font-size:em(24); line-height:em(36); padding:em(20) 0;
                    @include boxSizing;
                }
                input[type="text"]{
                    width:100% !important; font-size:em(24) !important; line-height:em(36) !important;
                    @include boxSizing;
                }
                .basic_txt_area{
                    width:100% !important; height:200px !important; font-size:em(24); line-height:em(36); padding:10px 15px;
                    @include boxSizing;
                }
                .selectForm{width:100% !important; margin:em(20) 0 0 !important;}
                .selectForm:first-child{margin:0 !important;}
            }
            .inquiry_btn{color:$white; padding:em(20) em(40); margin:em(20) 0 0; font-size:em(24); line-height:em(24);}
        }
    }
}