@charset "UTF-8";
/* 스마트 미세먼지 클린특화단지 - 	SmartList */
.SmartList{
    .sub_contents{
        width:1280px; padding:0 20px 0; margin:20px auto 150px;
        @include boxSizing;
        .estate_list_wrap li{
            position:relative; padding:60px 0; border-bottom:1px solid $gray6;
            @extend %clearfix;
            &:last-child{border:none;}
            div{float:left;}
            .estate_img{width:330px;}
            .txt_wrap{width:860px; margin:0 0 0 50px;}
            strong{font-size:32px; letter-spacing:-1px; line-height:32px; color:$blue1; font-weight:$bold;}
            p{
                color:$gray2; font-weight:$regular; margin:20px 0 10px;
                @include fontRul(16px);
            }
            span{
                color:$gray2; font-weight:$regular; display:block;
                @include fontRul(14px);
            }
            a{
                width:140px; height:50px; border:1px solid $gray5; font-size:16px; line-height:50px; letter-spacing:-.5px; color:$gray2; font-weight:$bold; display:block; text-align:center; margin:40px 0 0;
                @include borderRadius(50px);
                @include transition(all);
            }
            a:hover{color:$white; background-color:$primary; border-color:$primary;}
        }
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:0 em(30) 0;
            @include boxSizing;
            .estate_list_wrap li{
                padding:em(100) 0;
                &:first-child{padding:0 0 em(100);}
                &:last-child{border:none;}
                div{float:none;}
                .estate_img{width:100%;}
                .estate_img img{width:100%;}
                .txt_wrap{width:100%; margin:em(40) 0 0 0;}
                strong{width:100%; display:block; font-size:em(40); letter-spacing:-.5px; line-height:em(52);}
                p{margin:em(30) 0 em(20); font-size:em(24); line-height:em(40);}
                span{font-size:em(22); line-height:em(34);}
                a{
                    width:em(240); height:em(80); font-size:em(24); line-height:em(80); letter-spacing:0; margin:em(40) auto 0;
                }
            }
        }
    }

}


/* 스마트 미세먼지 클린특화단지 상세페이지 - SmartDetail */
.SmartDetail{
    .sub_contents{
        width:1280px; padding:0 20px 0; margin:0 auto 150px;
        @include boxSizing;
        .estate_area{
            position:relative; padding:60px 0; border-bottom:1px solid $gray6;
            @extend %clearfix;
            div{float:left;}
            .estate_img{width:330px;}
            .txt_wrap{width:860px; margin:0 0 0 50px;}
            strong{
                color:$blue1; font-weight:$bold;
                @include fontRul(32px);
            }
            span{
                color:$blue1; font-weight:$bold; display:block; margin:20px 0 0;
                @include fontRul(22px);
            }
            p{
                color:$gray2; font-weight:$regular; margin:10px 0 10px;
                @include fontRul(16px);
            }
            ul li{
                color:$gray2; font-weight:$regular; padding:0 0 0 15px;
                @include fontRul(16px);
                @include boxSizing;
                &:before{width:3px; height:3px; content:""; display:inline-block; background-color:$gray2; vertical-align:middle; position:relative; left:-10px;}
            }
        }
        .estate_info_wrap{
            .txt_wrap{
                margin:60px 0 0;
                strong{
                    color:$blue1; font-weight:$bold; display:block; margin:0 0 10px;
                    @include fontRul(22px);
                }
                ul li{
                    color:$gray2; font-weight:$regular; padding:0 0 0 12px;
                    @include fontRul(16px);
                    @include boxSizing;
                    &:before{width:3px; height:3px; content:""; display:inline-block; background-color:$gray2; vertical-align:middle; position:relative; left:-10px;}
                    span{display:block; margin:0 0 0 15px;}
                }

            }
            .estate_info_img{
                margin:50px 0 0 ;
                ul{display:flex; flex-direction:row; justify-content:space-between;}
                ul span{display:inline-block; font-size:16px; letter-spacing:-.5px; line-height:22px; color:$blue3; font-weight:$regular; margin:16px 0 0;}
                ul.estate_subway,
                ul.estate_myArea{text-align:center;}
                div span{display:block; font-size:16px; letter-spacing:-.5px; line-height:22px; color:$blue3; font-weight:$regular; margin:16px 0 0;}
            }
        }
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:em(30) auto em(150); padding:0 em(30);
            @include boxSizing;
            .estate_area{
                padding:0 0 em(60);
                div{float:none;}
                .estate_img{width:100%;}
                .estate_img img{width:100%;}
                .txt_wrap{width:100%; margin:em(40) 0 0 0;}
                strong{width:100%; display:block; font-size:em(40); letter-spacing:-.5px; line-height:em(52);}
                span{margin:em(40) 0 0; font-size:em(32); letter-spacing:-.5px; line-height:em(44);}
                p{margin:em(20) 0; font-size:em(24); line-height:em(40);}
                ul li{
                    font-size:em(22); line-height:em(34);
                    &:before{width:3px; height:3px; content:""; display:inline-block; background-color:$gray2; vertical-align:middle;}
                }
            }
            .estate_info_wrap{
                .txt_wrap{
                    margin:em(80) 0 0;
                    strong{font-size:em(32); letter-spacing:-.5px; line-height:em(44); margin:0 0 em(18);}
                    ul li{
                        font-size:em(22); line-height:em(34);
                        &:before{width:3px; height:3px; content:""; display:inline-block; background-color:$gray2; vertical-align:middle;}
                        span{display:block; margin:0 0 0 15px;}
                    }

                }
                .estate_info_img{
                    margin:em(50) 0 0 ;
                    & > img{width:100%;}
                    ul{display:flex; flex-direction:row; justify-content:space-between;}
                    ul span{display:block; font-size:em(24); letter-spacing:0; line-height:em(36); margin:em(20) 0 0;}
                    ul.estate_subway,
                    ul.estate_myArea{text-align:center; flex-wrap:wrap; justify-content:space-around;}
                    ul.estate_myArea li{margin:0 0 em(40);}
                    ul.estate_myArea li img{width:auto;}
                    ul.estate_subway li{margin:0 0 em(40);}
                    ul.estate_subway li img{width:100%;}
                    div span{display:block; font-size:em(24); letter-spacing:0; line-height:em(36); margin:em(20) 0 0;}
                    div img{width:100%;}
                }
            }
        }
    }
}

/* 미세먼지 저감효과 - ReductionEffect */
.ReductionEffect{
    .sub_visual{
        text-indent:0 !important; text-align:center; color:$white;
        .txt_wrap{display:inline-block; vertical-align:middle;margin:150px 0 0;}
        strong{font-size:40px; font-weight:$regular;}
        p{margin:10px 0 0; @include fontRul(20px);}
    }
    .sub_contents{
        width:1280px; padding:0 20px 0; margin:0 auto 300px;
        @include boxSizing;
        .effect_area{
            position:relative; padding:60px 0; border-bottom:1px solid $gray6;
            @extend %clearfix;
            div{float:left;}
            .effect_img{width:330px;}
            .txt_wrap{width:860px; margin:0 0 0 50px;}
            strong{
                color:$blue1; font-weight:$bold;
                @include fontRul(32px);
            }
            span{
                color:$blue1; font-weight:$bold; display:block; margin:20px 0 0;
                @include fontRul(22px);
            }
            p{
                color:$blue1; font-weight:$bold; margin:10px 0 10px;
                @include fontRul(16px);
            }
            ul li{
                color:$gray2; font-weight:$regular; padding:0 0 0 10px;
                @include fontRul(16px);
                &:before{width:3px; height:3px; content:""; display:inline-block; background-color:$gray2; vertical-align:middle; position:relative; left:-10px;}
            }
        }
        .effect_info_wrap{
            .txt_wrap{
                border-top:1px solid $gray6;
                padding:80px 0 0;
                &:first-child{border:none;}
                strong{
                    color:$blue1; font-weight:$bold; display:block; margin:0 0 10px;
                    @include fontRul(22px);
                }
                ul li{
                    color:$gray2; font-weight:$regular;
                    @include fontRul(16px);
                    &:before{width:3px; height:3px; content:""; display:inline-block; background-color:$gray2; vertical-align:middle; position:relative; left:-10px;}
                }
                & > p{
                    color:$gray2; font-weight:$regular;
                    @include fontRul(16px);
                }
            }
            .reductionEffect_slick_wrap{position:relative;}
            .effect_slick{
                width:1240px; margin:30px auto 70px;
                @extend %clearfix;
                .slick-track{padding:0 0 20px; margin:0;}
                .slick-slide{
                    width:295px; background-color:$white; float:left; padding:0 0 40px; margin:0 10px 0; border:1px solid $gray6;
                    @include boxSizing;
                    &:last-child{margin:0;}
                    &:hover,
                    &:focus{@include boxShadow(5px 5px 10px $gray7);}
                }
                a{width:100%; height:100%; display:block;}
                img{width:100%;}
                strong{
                    width:200px; height:60px; display:block; color:$blue3; font-weight:$bold; font-size:22px; letter-spacing:-1px; line-height:30px; margin:24px 0 14px; padding:0 0 0 30px;
                    @include multi-text-overflow(2);
                }
                p{
                    width:220px; height:40px; display:block; color:$gray2; font-weight:$regular; font-size:15px; letter-spacing:-.5px; line-height:20px; padding:0 0 0 30px;
                    @include multi-text-overflow(2);
                }
            }
            button{
                width:38px; height:38px; display:block; background-color:$white; border:1px solid $gray7; text-indent:-9999px; float:left; position:absolute; top:176px;
                @include borderRadius(38px);
                @include transition(all);
                &:after{
                    width:10px; height:10px; content:""; display:block; position:absolute; top:12px; border-top:2px solid $gray5; border-left:2px solid $gray5;
                }
                &:last-child{margin:0 0 0 10px;}
                &.slick_prev{left:-60px;}
                &.slick_prev:after{left:14px; @include rotate(-45);}
                &.slick_next{right:-60px;}
                &.slick_next:after{left:10px; @include rotate(135);}
            }
            button:hover,
            button:active{background-color:$primary; border-color:$primary;}
            button:hover:after,
            button:active:after{border-color:$white;}
            .effect_slick_ui{display:none;}
            .effect_txt_wrap{margin:20px 0 0;}

            
        }
    }
    @include pc-b(){
        .sub_contents{
            .effect_info_wrap{
                button{display: none !important;}
                .effect_slick_ui{
                    width:em(360); height:em(44); margin:em(30) auto 80px; text-align:center; display:block;
                    .effect_indicater{
                        width:100%; height:2px; display:inline-block; position:relative; background-color:$gray6; vertical-align:middle;
                        div{width:em(72); height:100%; background-color:$blue2; position:absolute; overflow:hidden; text-indent:-9999px;}
                    }
                    .effect_index_wrap{
                        display:inline-block; vertical-align:middle; margin:0;
                        span{
                            width:em(26); display:inline-block; color:$gray2; font-weight:$bold; font-size:em(22);
                        }
                    }
                }
            }
        }
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:em(30) auto em(150); padding:0 em(30);
            @include boxSizing;
            .effect_area{
                padding:0 0 em(60);
                div{float:none;}
                .effect_img{width:100%;}
                .effect_img img{width:100%;}
                .txt_wrap{width:100%; margin:em(40) 0 0 0;}
                strong{width:100%; display:block; font-size:em(40); letter-spacing:-.5px; line-height:em(52);}
                span{margin:em(40) 0 0; font-size:em(32); letter-spacing:-.5px; line-height:em(44);}
                p{margin:em(20) 0; font-size:em(24); line-height:em(40);}
                ul li{
                    font-size:em(22); line-height:em(34);
                    &:before{width:3px; height:3px; content:""; display:inline-block; background-color:$gray2; vertical-align:middle;}
                }
            }
            .effect_info_wrap{
                .txt_wrap{
                    padding:em(80) 0 0;
                    strong{font-size:em(32); letter-spacing:-.5px; line-height:em(44); margin:0 0 em(10);}
                    ul li{
                        font-size:em(22); line-height:em(34);
                        &:before{width:3px; height:3px; content:""; display:inline-block; background-color:$gray2; vertical-align:middle;}
                    }
                    & > p{margin:em(20) 0; font-size:em(24); line-height:em(40);}
                }
                .reductionEffect_slick_wrap{position:relative;}
                .effect_slick{
                    width:100%; margin:em(50) auto em(40);
                    .slick-track{padding:0 0 em(20);}
                    .slick-slide{
                        width:em(350); padding:0 0 em(36); margin:0 em(10) 0;
                        @include boxSizing;
                        &:last-child{margin:0;}
                        &:hover,
                        &:focus{@include boxShadow(5px 5px 10px $gray7);}
                    }
                    a{width:100%; height:100%; display:block;}
                    img{width:100%;}
                    strong{width:em(320); height:em(86); font-size:em(32); letter-spacing:0; line-height:em(42); margin:em(30) 0 em(12); padding:0 0 0 em(30);}
                    p{width:em(280); height:em(60); font-size:em(22); letter-spacing:0; line-height:em(30); padding:0 0 0 em(30);}
                }
                button{display: none !important;}
                .effect_slick_ui{
                    width:em(360); height:em(44); margin:em(30) auto em(80); text-align:center; display:block;
                    .effect_indicater{
                        width:100%; height:2px; display:inline-block; position:relative; background-color:$gray6; vertical-align:middle;
                        div{width:em(60); height:100%; background-color:$blue2; position:absolute; overflow:hidden; text-indent:-9999px;}
                    }
                    .effect_index_wrap{
                        display:inline-block; vertical-align:middle; margin:0;
                        span{
                            width:em(26); display:inline-block; color:$gray2; font-weight:$bold; font-size:em(22);
                        }
                    }
                }
                .effect_txt_wrap{margin:em(20) 0 0;}
            }
        }
    }
}

