@charset "UTF-8";
/*부천시청 로그인 css */
.login_wrap{
    .sub_contents{
        width:1280px; margin:0 auto 150px; padding:50px 150px 0;
        @include boxSizing;
        // 부천시청 css
        .w_box1 + .ui1300-log-title{margin-top:50px;}
        .ui1300-log-title{text-align:center;}
        .ui1300-log-title > span{font-size:26px; color: #454545;}
        .ui1300-log-title p{font-size:15px; color: #5f5f5f; line-height:24px; margin-top:30px;}
        .ui1300-log-alldiv{margin-top:30px; border-top:2px solid #363636; border-bottom:1px solid #363636; padding:20px 0 30px;}
        .ui1300-log-mbUl li{float:left; text-align:center;}
        .ui1300-log-mbUl li a{padding:20px 0; display:inline-block; color: #5f5f5f; background-color: #dcdcdc; width:185px; font-size:18px;}
        .ui1300-log-mbUl li a.on{background-color: #58a110; color: #fff;}
        .ui1300-log-mbUl li:first-child{margin-right:5px;}
        .ui1300-log-inf{background-color: #fbfbfb; border:1px solid #d0d0d0; padding:50px 15px; margin-top:30px;}
        .ui1300-log-infBg01{background:url("/img/cs/login/ui1300_01.gif") no-repeat 53px center; min-height:160px;}
        .ui1300-log-infBg01.log-addcss{width: 835px; margin: 0 auto; }

        .ui1300-log-infBg02{width:575px; margin-left:248px; position:relative;}
        .ui1300-logInpt span{display:block;}
        .ui1300-logInpt span.first{margin-bottom:10px;}
        .ui1300-logInpt span label{font-size:15px; color: #5f5f5f; display:inline-block; width:60px; margin-right:20px;}
        .ui1300-logInpt span input[type="text"]{width:60%; height:44px; border:1px solid #d0d0d0; padding-left:10px; background-color:$white;}
        .ui1300-logInpt span input[type="password"]{width:60%; height:44px; border:1px solid #d0d0d0; padding-left:10px; background-color:$white;}
        .ui1300-logBtn{text-align:center; width:125px; height:105px; line-height:105px; background-color:$primary; position:absolute; right:0; top:0px;}
        .ui1300-logBtn span{font-size:15px; color: #fff;}
        .ui1300-logNot{font-size:15px; color: #5f5f5f; letter-spacing:-1px; padding-left:80px; margin-top:15px;}
        .ui1300-logNot strong{color: #d20000;}
        .ui1300-log-tm01{text-align:center; margin-top:30px;}
        .ui1300-log-tm01 ul{display:inline-block;}
        .ui1300-log-tm01 ul li{float:left; text-align:center; margin-left:5px;}
        .ui1300-log-tm01 ul li:first-child{margin-left:0px;}
        .ui1300-log-tm01 ul li a{font-size:15px; color: #6c6c6c; display:inline-block; border:1px solid #b7b7b7; padding:15px 0; width:192px;}
        .ui1300-log-tm01 ul li a.on{background-color: #2b3e61; border:1px solid #2b3e61; color: #fff;}
        .ui1300-log-tm01 ul li a:hover{background-color: #2b3e61; border:1px solid #2b3e61; color: #fff;}

        .ui1301-blogAll{text-align:center; margin-top:30px;}
        .ui1301-blogUl{display:inline-block;}
        .ui1301-blogUl > li{float:left; padding:40px 0; border:1px solid #d0d0d0; width:398px; text-align:center;}
        .ui1301-blogUl > li:first-child{margin-right:40px;}
        .ui1301-blogUl > li > ul > li.first > span{font-size:24px; color: #353535; background:url("/img/cs/login/ui1301_01.gif") no-repeat left center; padding-left:45px; min-height:47px; display:inline-block; line-height:47px;}
        .ui1301-blogUl > li > ul > li.second > a{font-size:15px; color: #fff; display:inline-block; background-color: #1286c8; width:275px; padding:17px 0; margin-top:20px;}
        .ui1301-blogUl > li > ul > li.third > span{font-size:15px; color: #505050; display:inline-block; margin-top:17px; line-height:25px;}
        .ui1301-blogUl > li > ul > li.first.bg01 > span{background:url("/img/cs/login/ui1301_02.gif") no-repeat left center;}
        .ui1301-blogUl > li > ul > li.second.bg01 > a{background-color: #018293;}

        .security_program{box-sizing:border-box;width:890px;max-width:100%;margin:30px auto 0;}
        #sub-area .security_program{width:100%;}
        .security_program .tit{position:relative;padding-left:12px;}
        .security_program .tit:before{content:"*";position:absolute;left:0;top:3px;}
        .security_program .item_box{position:relative;display:table;}
        .security_program .item_box > li{display:table-cell;vertical-align:middle;padding:10px 0 10px 12px;box-sizing:border-box;}
        .security_program .item_box .item input{vertical-align:-2px;}
        .security_program .item_box .item + .item{margin-top:5px;}
        .security_program button.btn-apply{border:none;min-width:100px;min-height:36px;;cursor:pointer;}
        .security_program button.btn-apply span{position:relative;}

        .num_list > li{position:relative;padding-left:17px !important;margin-top:5px;background:none !important;}
        .num_list > li .num{position:absolute;left:0;top:-2px;font-style:normal;}

        .arr_list{margin-top:5px;}
        .arr_list > li{position:relative;padding-left:15px !important;background:none !important;margin-top:4px !important;}
        .arr_list > li:before{content:'→';position:absolute;left:0;top:0;}

        .dot_list{margin-top:5px;}
        .dot_list > li{position:relative;padding-left:10px !important;background:none !important;margin-top:4px !important;}
        .dot_list > li:before{content:'';position:absolute;left:0;top:9px;width:3px;height:3px;border-radius:50%;background:#666;}

        .dash_list > li{position:relative;padding-left:10px !important;background:none !important;margin-top:4px;}
        .dash_list > li:before{content:'-';position:absolute;left:0;top:0;}

        .add_list > li{position:relative;padding-left:15px !important;background:none !important;margin-top:4px !important;}
        .add_list > li:before{content:'※';position:absolute;left:0;top:0;}

        .etc_list > li{position:relative;padding-left:10px !important;background:none !important;margin-top:4px !important;}
        .etc_list > li:before{content:'*';position:absolute;left:0;top:3px;}

        .han_list{margin-top:10px;}
        .han_list > li{position:relative;padding-left:22px !important;background:none !important;min-height:18px;}
        .han_list > li + li{margin-top:2px;}
        .han_list > li .t01{position:absolute;left:0;top:0;font-style:normal;}

        .txt_list li + li{margin-top:10px;}
        .txt_list li .t01,
        .txt_list li .t02{display:inline-block;}
        .txt_list li .t01 + .t02{margin-left:10px;}

        .list-decimal{counter-reset:item;}
        .list-decimal > li{position:relative;padding-left:13px;line-height:18px;}
        .list-decimal > li:before{content:counter(item) ". ";counter-increment:item;position:absolute;left:0;top:0;}
        .list-decimal > li + li{margin-top:5px;}

        .list-dash > li{position:relative;padding-left:13px;line-height:18px;background:none;}
        .list-dash > li:before{content:"-";position:absolute;left:0;top:3px;color:#444;}

        .number_lst{position:relative;counter-reset:num-item;}
        .number_lst > li{position:relative;padding-left:15px;margin-top:15px;}
        .number_lst > li:before{z-index:2;content:counter(num-item)".";counter-increment:num-item;position:absolute;left:0;top:0;display:inline-block;}
        .number_lst > li:nth-child(10n){padding-left:20px;}


        .board-btn a{display:inline-block;margin-bottom:5px;min-width:60px;text-align:center;font-weight:bold;overflow:hidden;}
        .board-btn a:hover{color:#fae87f;text-decoration:none;}
        .btn-blue-white{background:$primary;color: #fff;font-size:15px;display:inline-block;padding:8px 15px;}
        .btn-blue-white{color: #fff;font-size: 15px;}
        .btn-blue-white:hover{color: #fff !important;font-size: 15px;}
        .btn-blue-white2{background:#0375c2;color: #fff;font-size:15px;margin-top: 50px;border-radius: 15px;display:inline-block;padding:15px 22px;width:154px;}
        .btn-purple-white{background:#4461a1;color: #fff;font-size:15px;display:inline-block;padding:8px 15px;}
        .btn-gray-white{background:#797979;color: #fff;font-size:15px;display:inline-block;padding:8px 15px;}
        .btn-white-gray{color:#797979;font-size:15px;display:inline-block;padding:8px 15px;border:1px solid #797979;}
        .board-btn a.btn-white-gray:hover{color: #28689f;}

        .rnew_view{display:inline-block; padding:5px 10px; margin:0 2px; background:#702dae; font-size:13px; font-weight:bold; color:#fff;}
        .file_view{display:inline-block; padding:5px 10px; margin:0 2px; background:#2d68ae; font-size:13px; font-weight:bold; color:#fff;}



        body #sub-area, #sub-area input, #sub-area textarea, #sub-area select, #sub-area button{font-size:14px;}
        .h3-title{font-size:15px;background:url(/img/cs/login/h3_title.gif) no-repeat left top; min-height:21px; margin-top:32px; line-height:20px; margin-bottom:10px; padding-left:26px;}
        .h3-title.mt0{margin-top:0;}

        .col-black{color: #454545;}
        .colorOrange{color: #e9684b; }
        .colorOrange02{color: #eb6100; }
        .colorNams{color: #2b3e61;}
        .colorYenddu{color: #14a174;}
        .colorNaoc{color: #58a110;}
        .colorNaoc2{color: #8fc31f;}
        .colorBlue02{color: #214bb6;}
        .colorBlue03{color: #0070cf;}
        .colorBlue04{color: #0375c2;}
        .colorBlue05{color: #394b6c;}
        .colorBlue06{color: #0a65c2;}
        .colorPal{color: #2c67d4;}
        .colorPal02{color: #1c5fcd;}
        .colorRed01{color: #d72828;}
        .colorRed02{color: #d80000;}
        .colorRed03{color: #f06979;}
        .colorRed04{color: #d91111;}
        .colorPink{color: #e62e8b;}

        // 로그인 공통 클레스
        .col-blue{color:$primary !important;}
        .clearfix:after{content:"";display:block;height:0;clear:both;visibility:hidden;}
        .hidden {overflow: hidden;position: absolute;width: 0;height: 0;top:-9999px;left:-9999px;line-height: 0;text-indent: -999em;margin: 0;padding: 0;background: none;line-height: 0;border: none;}
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:em(40) em(30) 0;
            // 부천시청 css
            .ui1300-log-title > span{font-size:em(34);}
            .ui1300-log-title p{font-size:em(24); line-height:em(38); margin-top:em(40);}
            .ui1300-log-mbUl li{width:49%;}
            .ui1300-log-mbUl li a{width:100%; font-size:em(32)}
            .ui1300-log-infBg01{background:none;}
            .ui1300-log-infBg02{width:100%; margin:0;}
            .ui1300-logInpt span label{display:none;}
            .ui1300-logNot{padding:0;}
            .ui1300-logInpt{width: calc(100% - 125px);padding-right: 125px;}
            .ui1300-logInpt span input[type="text"] {width: calc(100% - 20px);}
            .ui1300-logInpt span input[type="password"] {width: calc(100% - 20px);}
            .ui1300-log-tm01 ul{width:100%; display:block;}
            .ui1300-log-tm01 ul li{width: calc(98% / 3);margin-left: 1%;}
            .ui1300-log-tm01 ul li{width: calc(98% / 3);margin-left: 1%;}
            .ui1300-log-tm01 ul li a{width: calc(100% - 2px);font-size: 14px;}
        }
    }
}
