@charset "UTF-8";
/* 미세먼지 코디네이터 경력 관리 - CoordinatorCareerList */
.CoordinatorCareerList{
    .sub_contents{
        width:1280px; margin:0 auto 150px; padding:20px 20px 0;
        @include boxSizing;
        .career_table_wrap table{
            th,td{border:1px solid $gray6; padding:20px;}
            th{background-color:$bg-gray1; font-size:15px; color:$gray2; font-weight:$bold;}
            p{display:inline-block;}
        }
        .codi_cta{
            font-size:14px; color:$white; background-color:$gray3; padding:10px 16px; display:inline-block; margin:0 0 0 10px;
            @include borderRadius(30px);
        }
        .codi_cont_wrap{margin:60px 0;}
        .cont_header{
            margin:0 0 20px;
            @extend %clearfix;
            .codi_cta_wrap{float:right;}
            strong{
                color:$blue1; font-weight:$bold;
                @include fontRul(22px);
            }
        }
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:em(40) em(30) 0;
            .career_table_wrap .boardListTable table{
                width:100%; display:block; border-bottom:1px solid $gray6;
                tbody{width:100%; display:block; background-color:$bg-gray1; border-left:1px solid $gray6; border-right:1px solid $gray6; @include boxSizing;}
                tr{width:100%; display:block;}
                tr:after{display:block; clear:both; content:"";}
                th,td{float:left; display:block; padding:em(20); border:none; border-top:1px solid $gray6; @include boxSizing;}
                th{width:40%; font-size:em(24);}
                td{width:60%; background-color:$white; border-left:1px solid $gray6;}
                p{display:inline-block; line-height:em(36);}
                .codi_cta{display:block; margin:em(20) 0 0 em(10);}
            }
            .codi_cta{font-size:em(22); padding:em(20) em(40);}
            .codi_cont_wrap{margin:60px 0;}
            .cont_header{
                margin:0 0 20px;
                .codi_cta_wrap{float:right;}
                strong{font-size:em(30);}
            }
        }
    }
}

