@charset "UTF-8";

@import "./_common/base";
@import "./layout";
@import "./main";
@import "./login";
@import "./sttusFrcst";
@import "./reduction";
@import "./effect";
@import "./square";
@import "./knowledge";
@import "./myservice";
@import "./coordicareer";
/* common css */
.hidden-txt, legend { width: 0; height: 0; overflow: hidden; display: inline-block; text-indent: -9999px; line-height: 1px; position: absolute; }
#container{
   margin:120px 0 0;
   .sub_visual{
      width:100%; height:400px; background-repeat:no-repeat; background-position:50% 0; overflow:hidden; text-indent:-9999px;
   }
   .breadcrumb_wrap{
      width:100%; height:50px; background-color:$bg-gray1; border-bottom:1px solid #e7e9ec;
      @include boxSizing;
      &>div{
         width:1280px; height:100%; margin:0 auto;
         &>a{
            width:50px;height:100%; display:block; border-left:1px solid #e7e9ec; border-right:1px solid #e7e9ec; text-indent:-99999px; float:left;
            @include background("/img/cs/common/icon_home.jpg", 50%, 50%, 15px 13px);
         }
      }
      .crumb_depth{
         min-width:210px; height:100%; position:relative; top:0; border-right:1px solid #e7e9ec; border-left:1px solid $bg-gray1;
         float:left; line-height:50px;
         strong{
            color:$gray2; padding:0 50px 0 20px;
            @include fontRul(15px);
         }
         &>ul{
            width:100%; height:0; position:relative; overflow:hidden; opacity:0; z-index:10;
            @include transition();
            li{
               width:100%; height:30px;
               a{
                  width:100%; height:100%; display:block; font-weight:$bold; color:$gray2;  padding:0 0 0 20px;
                  @include boxSizing();
                  @include fontRul(15px);
                  &:hover, &:focus{background-color:$bg-gray2;}
                  &.active{color:$primary;}
               }
            }
         }
         &.multiple_depth>span{
            width:14px; height:8px; display:block; position: absolute; top:22px; right: 20px; text-indent:-9999px;
            @include transition();
            &:before{
               width:8px; height:2px; display:block; content:""; background-color:#b6babe; position: absolute; top:2px; right: 5px;
               @include rotate(45);
            }
            &:after{
               width:8px; height:2px; display:block; content:""; background-color:#b6babe; position: absolute; top:2px; right: 0px;
               @include rotate(-45);
            }
         }
         &.multiple_depth:hover, &.multiple_depth:focus, &.multiple_depth.active{
            background-color:#fff; border:1px solid $gray2; border-bottom:none; top:-1px; overflow:visible;
            &>span{
               @include rotate(180);
               &:before, &:after{background-color:$gray2;}
            }
            &>ul{
               height: auto; background-color:#fff; border:1px solid $gray2; border-top:none; top:-2px; left:-1px;
               padding:2px 0 0; opacity:1;
            }
         }
      }

   }
   //1680
   @include pc-a(){
      margin:110px 0 0;
   }
   // 1080
   @include mo(){
      margin:em(230) 0 0;
       &.main_wrap{margin:em(140) 0 0;}
      .sub_visual{
          display:none;
      }
      .breadcrumb_wrap{
          display:none;
      }
   }
}
//기본 스타일
input[type=text],
textarea,
select {
   outline:none;
   @include transition(border-color, .2s);
   &:focus { border-color:$gray2 !important;}
}
::selection { background:#000; color:$white; text-shadow:none; }
::-moz-selection { background:$gray2; color:$white; text-shadow:none; }
img { vertical-align:top; image-rendering:-webkit-optimize-contrast;}
caption { width:1px; height:1px; overflow:hidden; margin:-1px;}

/*문의하기*/
#questionWrap{
    width:60px; height:100px; position:fixed; z-index:100; bottom:100px; right:40px; text-align:center;
    a{width:100%; height:100%; display:block; font-size:15px; color:$blue3;  font-weight:$bold;}
    div{
        width:60px; height:60px; background-color:$blue3; background-image:url("/img/cs/common/icon_question.png"); background-repeat:no-repeat; background-position:50%; margin:0 0 8px; overflow:hidden; text-indent:-9999px;
        @include borderRadius(30px);
        @include transition(all);
    }
    &.type_w a{color:$white;}
    &.type_w div{background-color:$white;}
    @include mo(){
        display:none;
    }
}

#subTitle {
    width:1280px; margin:80px auto 0; padding:0 20px;  font-weight:$bold; color:$blue1;
    @include boxSizing;
    h3 {@include fontRul(32px);}
    p {padding:40px 0 0; @include fontRul(22px);}

    @include mo{
        /*subTitle  mobile*/
        width:100%; margin:0 auto 0; padding:0 em(30);
        h3 {font-size:em(40); line-height:em(52);}
        p {padding:em(40) 0 0; font-size:em(30); line-height:em(42);}
    }
}

/*기본 테이블 스타일*/
.basic_table {
    width:100%; border-top:2px solid $gray4; border-bottom:2px solid $gray4;
    tr {
        border-top:1px solid $gray4;
        &:first-child {border-top:0;}
    }
    th {padding:20px; font-size:16px; color:$gray2; font-weight:$bold;}
    td {padding:20px;}
}
/*모바일 테이블 스크롤 스타일*/
@include mo(){
    .boardListTable{
        width:100%;overflow-x:scroll;
        & > table{width:800px; font-size:em(24); margin:0 auto;}
        & > table p,
        & > table a{font-size:em(24);}
        .textDivision{display:none !important;}
        .date_picker:nth-of-type(2){margin:em(20) 0 0;}
    }
}

/*기본 텍스트 타입 input/textarea - disabled 포함*/
input.basic_txt_input,
textarea.basic_txt_area {
    font-size:16px; font-weight:$regular; border:1px solid $gray5; padding:0 15px;
    @include borderRadius(2px);
    @include boxSizing;
    @include placeholder(16px, $gray4, auto);
}
input.basic_txt_input {
    height:50px;
    &:disabled {background-color:$bg-gray1; font-size:15px;}
}
textarea.basic_txt_area {line-height:28px; resize:none; padding:15px;}

/* form 등록 취소 버튼 */
.cta_wrap{
    width:100%; text-align:center; margin:60px 0 0;
    button,
    a.cta_btn{
        width:160px; height:50px; font-size:16px; line-height:50px; font-weight:$bold; color:$gray2; border:1px solid $gray5; display:inline-block; vertical-align:middle; margin:0 5px 0;
        @include borderRadius(50px);
        @include transition(all);
    }
    button:hover,
    button.active,
    a.cta_btn:hover{background-color:$primary; color:$white;}
    @include mo(){
        /* form 등록 취소 버튼 mobile */
        margin:em(80) 0 0;
        button,
        a.cta_btn{
            width:em(190); height:em(80); font-size:em(24); line-height:em(80); margin:0;
        }
    }
}
.boardBtns{
    @include mo(){
        /* form 등록 취소 버튼 mobile */
        margin:em(80) 0 0;
        .btn{width:em(200); height:em(80); font-size:em(24); line-height:em(80); margin:0 em(10) 0;}
        .btn:not(:last-child) {margin-right: 0;}
        .prevBtn{background-position-x: em(45);}
    }
}
/* 테이블 모바일 */
.boardLists th,
.boardLists td{
    line-height:20px;
    @include mo(){
        line-height:em(34);
    }
}
/* 테이블 조회 영역 모바일 */
.lookupArea {
    @include mo(){
        width:100%; padding:em(30) em(40) em(40);
        @include boxSizing;
        .displayFlex{display:block;}
        .dateForm{width:100% !important; margin:0 0 em(20);}
        .textDivision{display:none;}
        .label_text{margin:em(30) 0 em(20);}
        .selectForm{width:100% !important;margin:0 0 em(20);}
        .selectForm:nth-of-type(2){margin:0 0 em(20);}
        .textForm{width:100% !important; margin:0 0 em(20);}
        .basic_txt_input{width:100% !important; margin:0 0 em(20);}
        .submit_btn_wrapper{border-left:none; border-top:1px solid $gray6; padding:em(40) 0 0; margin:em(20) 0 0; text-align:center;}
        .float_left{float:none !important;}
        .float_right{float:none !important;}
        .btn{padding:0 em(120) 0; font-size:em(28)}
    }
}

/*폼*/
.dateForm {display: inline-block;height: 50px;line-height: 50px;font-size: 15px;border-radius: 4px;padding: 0 35px 0 15px;border: 1px solid #d6dade;background: #fff url("/img/pbb/common/date-icon.png") no-repeat 90% center / 15px;font-weight: 600;color: #696d75;box-sizing: border-box;}
/*개시판 버튼*/
.prevBtn {
    @include mo(){
        background-image:none;
    }
}
/* 팝업 */
.popUp_dimmed{
    width:100%; height:100vh; background-color:rgba(0,0,0,.5); position:fixed; top:0; left:0; z-index:9999;
    .popUp_wrap{
        width:1100px; max-height:90vh; padding:50px; position:absolute; top:50%; left:50%; background-color:$white;
        @include boxSizing;
        @include translate(-50%, -50%);
        .popUp_header{
            width:100%; padding:0 0 20px; margin:0 0 40px; border-bottom:1px solid $blue1; position:relative;
            strong{font-size:26px; letter-spacing:-.5px;}
            .pop_close{
                width:30px; height:30px; position:absolute; top:0; right:0; overflow:hidden; text-indent:9999px;
                &:after{
                    width:80%; height:1px; content:""; display:block; background-color:$gray4; position:absolute; top:10px; left:10%;
                    @include rotate(-45);
                }
                &:before{
                    width:80%; height:1px; content:""; display:block; background-color:$gray4; position:absolute; top:10px; left:10%;
                    @include rotate(45);
                }
            }
        }
        .boardLists{margin:30px 0;}
        .lookupArea{padding:20px 60px 20px 70px;}
        .checkboxLabel{padding-left:0;}
        .checkboxForm + .checkboxImg{position:relative; top:0; left:0;}
        .upload_info{display: inline; font-size:14px; letter-spacing:-.5px; color:$gray2;}
        .edc_more {font-size: 14px;color: #fff;background-color: #787b83;padding: 10px 16px;-webkit-border-radius: 30px;-moz-border-radius: 30px;border-radius: 30px;}
        .career_table_wrap table{
            th,td{border:1px solid $gray6; padding:10px 20px;}
            th{background-color:$bg-gray1; font-size:15px; color:$gray2; font-weight:$bold;}
            p{display:inline-block; line-height:em(26);}
        }
        .count_title strong{font-size:18px; color:$blue1; letter-spacing:-0.5px; font-weight:$bold;}
        .img_btn{color:$primary; font-size:14px; text-decoration:underline;}
        .tableFieldArea{line-height:em(26);}
    }
    @include mo(){
        /*popUp_dimmed mobile*/
        .popUp_wrap{
            width:90%; max-height:90vh; padding:em(30); overflow:hidden;
            .popUp_header{
                width:100%; padding:0 0 em(30); margin:0 0 em(40);
                strong{font-size:em(38); letter-spacing:-.5px;}
                .pop_close{
                    width:30px; height:30px; position:absolute; top:0; right:0; overflow:hidden; text-indent:9999px;
                    &:after{
                        width:80%; height:1px; content:""; display:block; background-color:$gray4; position:absolute; top:10px; left:10%;
                        @include rotate(-45);
                    }
                    &:before{
                        width:80%; height:1px; content:""; display:block; background-color:$gray4; position:absolute; top:10px; left:10%;
                        @include rotate(45);
                    }
                }
            }
            .popUp_content{max-height:70vh; overflow-y:scroll;}
            .boardLists{margin:30px 0;}
            .lookupArea{padding:em(30) em(40) em(40);}
            .checkboxLabel{padding-left:0;}
            .checkboxForm + .checkboxImg{position:relative; top:0; left:0;}
            .upload_info{width:80%; display:block; font-size:em(22); letter-spacing:0; margin:em(16) 0 0;}
            .boardView th{width:25%; height:auto; vertical-align:middle;}
            .cta_wrap{padding:0 0 em(40);}
            .boardListTable .textDivision {display: inline !important;}
            .logTxt{width:em(290);}
        }
    }
}


