@charset "UTF-8";
/* 현황 및 예보 - 미세먼지 수치 현황 	UlfptcNclSttusList */
.UlfptcNclSttusList{
    .sub_contents{
        margin:0 auto 150px;
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:em(80) em(30) 0;
        }
    }
}

/* 현황 및 예보 - 미세먼지 수치 예보 UlfptcNclFrcstList*/
.UlfptcNclFrcstList{
    .sub_contents{
        margin:0 auto 150px;
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:em(80) em(30) 0;
            .subpageSectionTitle > h4{font-size:em(30); line-height:em(42);}
            .introText p {font-size: em(24); line-height:em(36); padding-left:em(25);position:relative;}
        }
    }
}

/* 현황 및 예보 - 생활지수 정보 UlfptcIdexSctnInfoSearch*/
.UlfptcIdexSctnInfoSearch{
    .sub_contents{
        width: 1280px; margin: 0 auto 150px; padding: 40px 20px 0;
        @include boxSizing;
        .cdt_good, .cdt_usually,
        .cdt_bad, .cdt_terrible{color:$white;}
        .cdt_good{background-color:$cdt-blue;}
        .cdt_usually{background-color:$cdt-green;}
        .cdt_bad{background-color:$cdt-orange;}
        .cdt_terrible{background-color:$cdt-red;}
        .tableFieldArea > div{margin:5px 0 0;}
        .cartArea{padding:15px; @include boxSizing;}
        .cartArea img{width:100%;}
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:em(80) em(30) 0;
            .subpageSectionTitle > h4{font-size:em(30); line-height:em(42);}
            .introText p {font-size: em(24); line-height:em(36); padding-left:em(25);position:relative;}
        }
    }
}

/* 행동요령- UlfptcIdexSctnActCheatSearch */
.UlfptcIdexSctnActCheatSearch{
    .sub_contents{
        width:1280px; margin:0 auto 150px; padding:20px 20px 0;
        @include boxSizing;
        .action_list_wrap{
            @extend %clearfix;
            .list_box{width:50%; float:left;}
            .list_box li{
                margin:20px 0 40px; padding:10px 0 10px 100px;
                @include boxSizing;
                &.list01{background:url(/img/cs/sttusFrcst/icon_list_01.jpg) 0 50% no-repeat;}
                &.list02{background:url(/img/cs/sttusFrcst/icon_list_02.jpg) 0 50% no-repeat;}
                &.list03{background:url(/img/cs/sttusFrcst/icon_list_03.jpg) 0 50% no-repeat;}
                &.list04{background:url(/img/cs/sttusFrcst/icon_list_04.jpg) 0 50% no-repeat;}
                &.list05{background:url(/img/cs/sttusFrcst/icon_list_05.jpg) 0 50% no-repeat;}
                &.list06{background:url(/img/cs/sttusFrcst/icon_list_06.jpg) 0 50% no-repeat;}
                &.list07{background:url(/img/cs/sttusFrcst/icon_list_07.jpg) 0 50% no-repeat;}
            }
            .list_box li strong{
                font-weight:$bold; color:$primary;
                @include fontRul(20px);
            }
            .list_box li p{
                font-weight:$regular; color:$gray2; font-size:15px; letter-spacing:-.5px; line-height:22px; margin:5px 0 0;
            }
        }
        .ulfptc_cont_wrap{margin:60px 0;}
        .ulfptc_cont_wrap table{
            text-align:center; line-height:24px;
            th,td{border:1px solid $gray6; padding:10px;}
            th{background-color:$bg-gray1; font-size:15px; color:$gray2; font-weight:$bold;}
            p{display:inline-block;}
        }
        .ulfptc_header{
            margin:0 0 20px;
            @extend %clearfix;
            .codi_cta_wrap{float:right;}
            strong{
                color:$blue1; font-weight:$bold;
                @include fontRul(22px);
            }
        }

    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:em(40) em(30) 0;
            .action_list_wrap{
                .list_box{width:100%; float:none;}
                .list_box li{
                    margin:20px 0 40px; padding:10px 0 10px 100px;
                }
                .list_box li strong{font-size:em(32); line-height:em(46); letter-spacing:0; }
                .list_box li p{
                    font-size:em(24); letter-spacing:0; line-height:em(36);
                }
            }
            .career_table_wrap .boardListTable table{
                width:100%; display:block; border-bottom:1px solid $gray6;
                tbody{width:100%; display:block; background-color:$bg-gray1; border-left:1px solid $gray6; border-right:1px solid $gray6; @include boxSizing;}
                tr{width:100%; display:block;}
                tr:after{display:block; clear:both; content:"";}
                th,td{float:left; display:block; padding:em(20); border:none; border-top:1px solid $gray6; @include boxSizing;}
                th{width:40%; font-size:em(24);}
                td{width:60%; background-color:$white; border-left:1px solid $gray6;}
                p{display:inline-block; line-height:em(36);}
                .codi_cta{display:block; margin:em(20) 0 0 em(10);}
            }
            .codi_cta{font-size:em(22); padding:em(20) em(40);}
            .codi_cont_wrap{margin:60px 0;}
            .ulfptc_header{
                margin:0 0 20px;
                .codi_cta_wrap{float:right;}
                strong{font-size:em(30);}
            }
        }
    }
}