@charset "UTF-8";
/* 개인맞춤서비스 제공 - Personalized */
.Personalized{
    .personal_txtWrap{
        width:1280px; margin:26px auto 0;
        .personal_label{
            display:inline-block; vertical-align:middle; padding:4px 12px; background-color:$blue5; color:$primary; font-weight:$bold; margin:0 0 0 20px;
            @include fontRul(14px);
            @include borderRadius(20px);
        }
        p{
            display:inline-block; vertical-align:middle; color:$gray2; font-weight:$bold;
            @include fontRul(16px);
        }
    }
    .sub_contents{
        width:1280px; margin:0 auto 150px; padding:80px 20px 0;
        @include boxSizing;
        .float_wrap{
            width:100%; margin:0 0 50px;
            @extend %clearfix;
            & > div{
                float:left;
                &:first-child{width:600px; margin:0 40px 0 0;}
                &:last-child{width:590px;}
                .list_header{
                    width:100%; height:70px; position:relative; margin:0 0 30px;
                    strong{
                        color:$blue1; font-weight:$bold;
                        @include fontRul(22px);
                    }
                    .cdt_label{
                        display:inline-block; font-weight:$bold; color:$white; font-size:15px; letter-spacing:-.5px;
                        background-color:$red1; padding:6px 16px; vertical-align:top; margin:5px 0 0 5px;
                        @include borderRadius(24px);
                    }
                    .deadline{
                        position:absolute; top:5px; right:40px; color:$gray4; font-weight:$regular;
                        @include fontRul(14px);
                    }
                    a{
                        width:30px; height:30px; border:1px solid $gray7; overflow:hidden; text-indent:-9999px; background-color:$white; position:absolute; top:0; right:0;
                        @include borderRadius(5px);
                        &:after{width:2px; height:12px; content:""; display:block; background-color:$gray3; position:absolute; top:9px; left:14px;}
                        &:before{width:12px; height:2px; content:""; display:block; background-color:$gray3; position:absolute; top:14px; left:9px;}
                    }

                }
            }
            .cont_list_info_wrap{
                ul{
                    width:100%; position:relative;
                    li{
                        width:100%; color:$blue4; font-weight:$bold; background-color:$bg-gray2; padding:16px 20px; margin:10px 0 0;
                        @include fontRul(18px);
                        @include boxSizing;
                        @extend %clearfix;
                        &:first-child{margin:0;}
                        .txt_wrap{width:80%; display:inline-block;}
                        p{display:inline-block; vertical-align:middle;}
                        strong{display:inline-block; vertical-align:middle; margin:0 0 0 15px; color:$gray1;}
                        .cdt_good{color:$cdt-blue;}
                        .cdt_usually{color:$cdt-green;}
                        .cdt_bad{color:$cdt-orange;}
                        .cdt_terrible{color:$cdt-red;}
                        & > button{
                            width:30px; height:30px; border:1px solid $gray7; overflow:hidden; text-indent:-9999px; background-color:$white; position:relative; float:right;
                            @include borderRadius(5px);
                            &:after{width:2px; height:12px; content:""; display:block; background-color:$gray3; position:absolute; top:9px; left:14px;}
                            &:before{width:12px; height:2px; content:""; display:block; background-color:$gray3; position:absolute; top:14px; left:9px;}
                        }
                        .info_more{
                            width:100%; position:absolute; top:0; left:0; z-index:2; background-color: $white; border:1px solid $gray6; padding:25px 40px; display:none;
                            @include boxSizing;
                            @include opacity(0);
                            dl{
                                margin:10px 0 0;
                                dt{
                                    color:$blue3; font-weight:$bold; margin:em(15) 0 0;
                                    @include fontRul(16px);
                                }
                                dd{
                                    color:$gray2; font-weight:$bold;
                                    @include fontRul(15px);
                                }
                            }
                            .indent_2{
                                dd{margin:0 0 0 25px;}
                            }
                            button{
                                width:30px; height:30px; border:1px solid $gray7; overflow:hidden; text-indent:-9999px; background-color:$white; position:absolute; top:15px; right:15px;
                                @include borderRadius(5px);
                                &:before{width:12px; height:2px; content:""; display:block; background-color:$gray3; position:absolute; top:14px; left:9px;}
                            }
                        }

                    }

                }
            }
            .dust_info{
                .list_header{
                    border-bottom: 1px solid $gray6;
                    .deadline{right:0;}
                }
                .dust_graph{width:100%;}
                .cont_list_info_wrap{
                    width:100%; padding:0 10px; margin:30px 0 0;
                    @include boxSizing;

                }
            }
            .fit_wrap{
                .cont_list_info_wrap
                .list_header{
                    border-bottom: 1px solid $gray6;
                    .deadline{right:0;}
                }
                .info_wrap_2{margin:32px 0 0;}
            }
            .table_wrap1,
            .table_wrap2{
                .list_header{height:auto;}
                table{
                    text-align:center;
                    th,
                    td{padding:16px 0; border:1px solid $gray6;}
                    th{font-size:15px; line-height:18px; letter-spacing:-.5px; color:$gray2; font-weight:$bold;}
                    td{font-size:15px; line-height:18px; letter-spacing:-.5px; color:$blue3; font-weight:$bold;}
                    .b_line{padding:0;}
                    thead{background-color:$bg-gray1;}
                }
            }
            .calendar_wrap{
                .list_header{margin:0;}
                .calendar_table{
                    thead{
                        text-align:center; background-color:$bg-gray1; border:1px solid $bg-gray1;
                        th{
                            height:30px; color:$gray2; font-weight:$bold;
                            @include fontRul(14px);
                        }
                    }
                    tbody{
                        td{
                            height:100px; border:1px solid $gray6; position:relative; padding:0 10px 10px; vertical-align:top;
                            span{
                                display:block;
                                @include fontRul(13);
                            }
                            .denote{
                                width:25px; height:25px; color:$white; text-align:center; font-size:13px; line-height:25px; display:inline-block; margin:5px 0;
                                @include borderRadius(25px);

                            }
                        }
                    }
                    .sunday span{color:#ee4d5d;}
                }
            }
            .event_wrap{
                .list_header{
                    border-bottom: 1px solid $gray6;
                    .deadline{right:0;}
                    strong span{color:$primary;}
                }
                .event_list_info_wrap{
                    ul{
                        width:100%;
                        li{
                            width:100%; background-color:$bg-gray2; margin:10px 0 0; position:relative;
                            a{
                                width:100%; height:100%; display:block; padding:16px 20px;  color:$blue4; font-weight:$bold;
                                @include fontRul(18px);
                                @include boxSizing;
                            }
                            &:first-child{margin:0;}
                            .denote{
                                width:30px; height:30px; display:inline-block; vertical-align:middle; font-size:14px; line-height:30px; color:$white; font-weight:$bold; text-align:center;
                                @include borderRadius(30px);
                            }
                            p{
                                width:90%; display:inline-block; vertical-align:middle; margin:0 0 0 15px;
                                @include text-overflow;
                            }
                        }

                    }
                }
                .family_event{margin:0 0 32px;}
            }
        }
    }
    @include mo(){
        .personal_txtWrap{
            width:100%; margin:em(30) auto 0; padding:0 em(30);
            @include boxSizing;
            .personal_label{
                padding:em(8) em(24); margin:0; font-size:em(22); line-height:em(34);
                @include borderRadius(5px);
            }
            p{ padding:em(10) 0 0; font-size:em(24); line-height:em(36);}
        }
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:em(80) em(30) 0;
            @include boxSizing;
            .float_wrap{
                width:100%; margin:0;
                & > div{
                    float:none; margin:0 0 em(100);
                    &:first-child{width:100%; margin:0 em(50) em(100) 0;}
                    &:last-child{width:100%;}
                    .list_header{
                        width:100%; height:em(80); margin:0 0 em(30);
                        strong{font-size:em(30); line-height:em(42); letter-spacing:-.5px;}
                        .cdt_label{display:inline-block; font-size:em(15); letter-spacing:-.5px; padding:em(6) em(16); margin:em(5) 0 0 em(5);}
                        .deadline{top:5px; right:em(70); font-size:em(22); line-height:em(34);}
                    }
                }
                .cont_list_info_wrap{
                    ul{
                        li{
                            width:100%; height:em(100); padding:em(16) em(50) em(16) em(28); margin:em(10) 0 0; font-size:em(26); line-height:em(38);
                            @include boxSizing;
                            @include text-overflow;
                            .txt_wrap{
                                width:calc(100% - 30px); display:inline-block;
                                @include text-overflow;
                            }
                            p{
                                width:100%; line-height:em(68);
                                @include text-overflow;
                            }
                            strong{line-height:em(68); margin:0 0 0 em(15);}
                            & > button{display:inline-block; top:em(4); right:em(-25)}
                            .info_more{
                                padding:em(25) em(40);
                                @include boxSizing;
                                @include opacity(0);
                                .info_title p{width:auto; white-space:break-spaces; line-height:em(40); display:block;}
                                .info_title strong{margin:0;}
                                dl{
                                    margin:em(10) 0 0;
                                    dt{margin:em(25) 0 em(10); font-size:em(24); line-height:em(36);}
                                    dd{font-size:em(22); line-height:em(34); white-space:break-spaces;}
                                }
                                .indent_2{
                                    dd{margin:0 0 0 em(40);}
                                }
                            }
                        }
                    }
                }
                .dust_info{
                    .cont_list_info_wrap{padding:0; margin:em(40) 0 0;}
                }
                .fit_wrap{
                    .info_wrap_2{margin:em(80) 0 0;}
                }
                .table_wrap1,
                .table_wrap2{
                    table{
                        th,
                        td{padding:em(30) 0; }
                        th{font-size:em(22); line-height:em(24);}
                        td{font-size:em(22); line-height:em(24);}
                    }
                }
                .calendar_wrap{
                    .calendar_table{
                        thead{
                            th{height:em(50); font-size:em(22); line-height:em(34);}
                        }
                        tbody{
                            td{
                                height:em(100); padding:0 em(10) em(10);
                                span{display:block; font-size:em(21); line-height:em(33);}
                            }
                        }
                    }
                }
                .event_wrap{
                    .event_list_info_wrap{
                        ul{
                            li{
                                width:100%; height:em(100); margin:em(10) 0 0;
                                a{width:100%; height:100%; display:block; padding:em(16) em(28); font-size:em(26); line-height:em(38);}
                                .denote{width:em(50); height:em(50); display:inline-block; vertical-align:middle; font-size:em(22); line-height:em(50); margin:em(10) 0 0;}
                                p{width:80%; margin:em(10) 0 0 em(20);}
                            }
                        }
                    }
                    .family_event{margin:0 0 em(50);}
                }
            }
        }
    }
}

/* 개인맞춤서비스 제공 - 	MyInfoList */
.MyInfoList{
    .sub_contents{
        width:1280px; margin:0 auto 80px; padding:20px 20px 0;
        @include boxSizing;
        .myinfolist_wrap table{
            margin:0 0 50px;
            th,td{border:1px solid $gray6; padding:20px; line-height:20px; font-style:15px;}
            th{background-color:$bg-gray1; font-size:15px; color:$gray2; font-weight:$bold;}
            p{display:inline-block; font-size:15px; color:$gray2; stroke-linecap:-.5px;}
            thead th{text-align:center; font-size:16px; color:$blue4; padding:10px;}
            .radioLabel,
            .checkboxLabel{margin:10px 0 10px 20px;}
            .area_label .checkboxLabel,
            .area_label .radioLabel{width:70px;}
            .myinfo_cta{
                font-size:14px; color:$white; background-color:$gray3; padding:10px 16px; display:inline-block; margin:0 0 0 10px;
                @include borderRadius(30px);
            }
            .att_area_wrap{position:relative;}
            .my_area_btn{
                position:absolute; top:50%; right:280px;
                @include translate(0,-50%);
            }

        }
        .txt_wrap{margin:0 0 20px;}
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:em(40) em(30) 0;
            .myinfolist_wrap .boardListTable table{
                width:100%; margin:0 0 em(50);
                th,td{padding:em(30); line-height:em(30); font-size:em(24); }
                th{width:30%; font-size:em(24); line-height:em(36); vertical-align:top; }
                p{display:inline-block; font-size:em(24); color:$gray2; stroke-linecap:0;}
                thead th{font-size:em(24); padding:em(20);}
                .radioLabel,
                .checkboxLabel{margin:em(20) 0 em(20) em(30);}
                .area_label .checkboxLabel,
                .area_label .radioLabel{width:70px;}
                .myinfo_cta{
                    font-size:em(24); padding:em(20) em(40); display:inline-block; margin:em(20) 0 0 em(20);
                    @include borderRadius(30px);
                }
                .att_area_wrap{position:relative;}
                .my_area_btn{
                    position:relative; top:0; right:0;
                    @include translate(0,0);
                }
                .label_text{word-break:break-word;}

            }
            .txt_wrap{margin:0 0 20px;}
        }
    }
}

/* 개인맞춤서비스 제공 - 	MySvcList */
.MySvcList{
    .sub_contents{
        width:1280px; margin:0 auto 80px; padding:20px 20px 0;
        @include boxSizing;
        .svc_cont{margin:50px 0 0;}
        .svc_cont strong{display:block; margin:0 0 20px; font-size:22px; line-height:34px; color:$blue1; letter-spacing:-1px}
        .main_category button{width:170px; padding:6px 0; font-size:17px; line-height:29px; color:$gray2; border:1px solid $gray2;}
        .main_category li.active button{background-color:$primary; color:$white;}
        .category_wrap{
            @extend %clearfix;
            .middle_category{width:27%; float:left;}
            .Subclass{width:70%; float:right;}
            .Subclass .category_list{padding:10px 20px;}
            .Subclass .category_list li{width:32%; margin:10px 0;}
            .category_list{
                min-height:500px; background-color:$bg-gray1; border:1px solid $gray6; padding:20px;
                @include boxSizing;
                li{
                    padding:20px; background-color:$white; margin:0 0 10px; border:1px solid $gray6;
                    @include boxSizing;
                    &:last-child{margin:0;}
                }
            }
        }
    }
    @include mo(){
        .sub_contents{
            width:100%; margin:0 auto em(150); padding:em(40) em(30) 0;
            .svc_cont{margin:em(60) 0 0;}
            .svc_cont strong{display:block; margin:0 0 em(20); font-size:em(36); line-height:em(48); letter-spacing:-0.5px}
            .main_category{flex-wrap:wrap;}
            .main_category li{width:49%; margin:0 0 2%;}
            .main_category button{width:100%; padding:em(20) 0; font-size:em(24); line-height:em(36);}
            .main_category li.active button{background-color:$primary; color:$white;}
            .category_wrap{
                .middle_category{width:100%; float:none;}
                .Subclass{width:100%; float:none; margin:em(50) 0 0;}
                .Subclass .category_list{padding:20px;}
                .Subclass .category_list ul{flex-wrap:wrap;}
                .Subclass .category_list li{width:100%; margin:0 0 10px;}
                .Subclass .category_list li:last-child{width:100%; margin:0;}
                .category_list{
                    min-height:auto;
                    @include boxSizing;
                }
            }
        }
    }
}